@import '../../css/variables';

.search-form {
  display: flex;
  align-items: center;
  position: relative;
  min-height: 60px;
  margin-bottom: 10px;

  @media only screen and (max-width: $BREACKPOINT-550) {
    flex-direction: column;
    align-items: flex-start;
  }

  label {
    font-size: small;
  }
  .search-box,
  .num-box {
    -moz-appearance: none;
    -webkit-appearance: none;
    appearance: none;
    margin: 0 10px;
    display: block;
    padding: 3px;
    border: 1px solid $COLOR-SECONDARY-DARK;
    background-color: $COLOR-SECONDARY-LIGHT;

    &:hover, &:focus {
      border: 1px solid $COLOR-SECONDARY-DARK;
      background-color: white;
      box-shadow: 0 0 2px rgba(0,0,0,0.8);
    }
  }

  .num-box {
    width: 40px;
  }

  .search-icon {
    margin-left: auto;
    width: 3rem;
    height: 3rem;

    @media only screen and (max-width: $BREACKPOINT-550) {
      margin-left: 0;
    }
  }
}
