@import '../../css/variables';

.beer-card-container {
  margin: 0;
  padding: 10px;
  border: 1px solid $COLOR-PRIMARY-DARK;
  width: $BEER-CARD-WIDTH;
  height: $BEER-CARD-HEIGHT;
  display: flex;
  flex-direction: column;
  box-shadow: 4px 4px 8px $COLOR-SECONDARY-DARK;
  border-radius: 5px;
  background-color: white;
  overflow: hidden;

  &:hover {
    box-shadow: 4px 4px 30px $COLOR-SECONDARY-DARK;
    transform: scale(1.025);
    cursor: pointer;
    background-color: $COLOR-PRIMARY-LIGHT;
  }

  .image-container {
    margin: 0 auto;
    height: $BEER-IMG-HEIGHT;

    .preview-img {
      width: auto;
      max-width: calc($BEER-CARD-WIDTH - 20px);
      height: $BEER-IMG-HEIGHT;
      max-height: calc($BEER-CARD-WIDTH - 20px);
      border-radius: 5px;
    }
  }

  .product-info {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    padding-bottom: 5px;

    .product-name {
      font-weight: bold;
      text-transform: uppercase;
      color: $COLOR-SECONDARY-DARK;
      height: 3rem;
      overflow: hidden;
    }

    .product-tagline {
      font-size: small;
      border-top: 1px dotted lightgray;
      border-bottom: 1px dotted lightgray;
      height: 3.5rem;
      overflow: hidden;
    }

    .product-introduced {
      font-size: small;
      align-self: flex-end;
    }
  }

  .product-actions {
    display: flex;
    flex-direction: row;
    width: $BEER-CARD-WIDTH;
    margin-left: -10px;
    margin-bottom: -10px;
    padding: 0 14px 0 0;
    align-items: center;
    background-color: lightgray;
    height: 2.5rem;

    .btn-fav {
      width: 40px;
      height: 1.5rem;
      border: none;
      background-color: transparent;

      &:hover {
        cursor: pointer;

        .btn-icon {
          height: 1.2rem;
        }
      }

      .btn-icon {
        height: 1rem;
        width: auto;
        align-self: center;
      }
    }
  }
}
