@import '../../css/variables';


.modal {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 8;

  .modal-popup {
    $MODAL-WIDTH: min(80vw, $BEER-POPUP-WIDTH);
    width: $MODAL-WIDTH;
    max-width: $BEER-POPUP-WIDTH;
    background-color: $COLOR-SECONDARY-LIGHT;
    margin: 0 auto;
    border: 1px solid $COLOR-PRIMARY-DARK;
    box-shadow: 0px 6px 36px rgba(0, 0, 0, 0.6);
    position: absolute;
    top: 60px;
    left: calc(50% - $MODAL-WIDTH / 2);
    z-index: 10;

    .actions {
      width: 50px;
      height: 50px;
      position: absolute;
      top: -25px;
      left: calc(-25px + $MODAL-WIDTH);
      z-index: 11;

      .toggle-button {
        width: 50px;
        height: 50px;
        font-size: 2rem;
        font-weight: bold;
        border-radius: 50%;
        border: 2px solid white;
        padding: 0;
        text-align: center;
        background-color: $COLOR-SECONDARY-DARK;
        color: white;
      }
    }

    .card-details {
      display: flex;
      flex-direction: row;
      gap: 10px;
      padding-top: 40px;
      padding-bottom: 20px;
      margin: 0 20px;

      .img-container {
        width: 150px;
        height: 100%;
        padding: 20px 5px;
        margin-top: 40px;
        background-color: white;

        img {
          width: 140px;
          // height: 400px;
        }
      }

      .info-container {
        padding: 0 20px;

        .atr-label {
          padding-right: 8px;
          color: $COLOR-SECONDARY-DARK;
          font-weight: 500;
        }

        .item-name {
          font-weight: bold;
          font-size: 2rem;
          text-transform: uppercase;
          text-align: center;
          color: $COLOR-SECONDARY-DARK;

          @media only screen and (max-width: $BREACKPOINT-550) {
            font-size: 1.2rem;
          }
        }

        .item-tagline {
          text-align: center;
          font-size: 1.2rem;
          font-style: italic;
          margin-bottom: 12px;

          @media only screen and (max-width: $BREACKPOINT-550) {
            font-size: 0.8rem;
          }
        }

        .item-ebc {
          display: flex;
          flex-direction: row;
          gap: 10px;
          align-items: center;

          .ebc-box {
            width: 2rem;
            height: 1rem;
            border: 1px solid $COLOR-PRIMARY-DARK;
          }
        }

        .item-description {
          margin: 8px 0;
          padding: 0 10px;

          @media only screen and (max-width: $BREACKPOINT-550) {
            font-size: small;
          }
        }

        .item-food {
          .bullet-list {
            padding: 0 20px;

            @media only screen and (max-width: $BREACKPOINT-550) {
              font-size: small;
            }
          }
        }

        .item-tips {
          border-top: 1px dotted $COLOR-SECONDARY-DARK;
          margin-top: 16px;
          padding: 0 10px;

          @media only screen and (max-width: $BREACKPOINT-550) {
            display: none;
          }
        }

      }
    }
  }

  .modal-back {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    z-index: 9;
    background-color: rgba(0, 0, 0, 0.6);
  }

}
